'use strict';

// MODULES //

import { isPrimitive as isString } from '@stdlib/assert-is-string';
import { isPrimitive as isNonNegativeInteger } from '@stdlib/assert-is-nonnegative-integer';
import nextGraphemeClusterBreak from '@stdlib/string-next-grapheme-cluster-break';
import format from '@stdlib/error-tools-fmtprodmsg';


// MAIN //

/**
* Returns the first visual character(s) of a string.
*
* @param {string} str - input string
* @param {NonNegativeInteger} [n=1] - number of characters to return
* @throws {TypeError} must provide a string
* @throws {TypeError} second argument must be a nonnegative integer
* @returns {string} output string
*
* @example
* var out = first( 'last man standing' );
* // returns 'l'
*
* @example
* var out = first( 'presidential election' );
* // returns 'p'
*
* @example
* var out = first( 'javaScript' );
* // returns 'j'
*
* @example
* var out = first( 'Hidden Treasures' );
* // returns 'H'
*
* @example
* var out = first( 'ðŸ¶ðŸ®ðŸ·ðŸ°ðŸ¸', 2 );
* // returns 'ðŸ¶ðŸ®'
*
* @example
* var out = first( 'foo bar', 5 );
* // returns 'foo b'
*/
function first( str, n ) {
	var nextBreak;
	if ( !isString( str ) ) {
		throw new TypeError( format( 'null3R', str ) );
	}
	if ( arguments.length > 1 ) {
		if ( !isNonNegativeInteger( n ) ) {
			throw new TypeError( format( 'null3k', n ) );
		}
		nextBreak = 0;
		while ( n > 0 ) {
			nextBreak = nextGraphemeClusterBreak( str, nextBreak );
			n -= 1;
		}
	} else {
		nextBreak = nextGraphemeClusterBreak( str );
	}
	// Value of `nextBreak` will be -1 if and only if `str` is an empty string or `str` has only 1 extended grapheme cluster...
	if ( str === '' || nextBreak === -1 ) {
		return str;
	}
	return str.substring( 0, nextBreak );
}


// EXPORTS //

export default first;
